.burgerNav {
  display: none;
  @media screen and (max-width: 499px) {
    width: 340px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: #a9adb8;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.6em;
    .link {
      padding: 15px 0;
      font-size: 15px;
      display: inline-block;
      color: #fdfeff;
      text-transform: uppercase;
      font-family: 'Roboto', sans-serif;
      font-weight: 500;
      &:hover {
        cursor: pointer;
        color: #fec544;
        font-size: 18px;
        letter-spacing: 2px;
        transition: 0.3s;
      }
    }
  }
}
